/* Home css */
.home-card {
    margin: 3%
}

/* Card css */

.card-link {
    text-decoration: none;
    color: black
}

/* App Bar css */

.app-bar {
    background-color: #ff884d

}
.app-bar-link {
    text-decoration: none;
    color: white
}

/* Image Upload css */

.image-uploader {
    display: flex;
}

/* Cancer Detection App */

.cancer-detection-title {
    text-align: center;
    padding-top: 2%;
    width: 50%
}

.cancer-detection-paragraph {
    text-align: center;
    padding-top: 0.5%;
    width: 50%
}

.cancer-detection-text-wrapper {
    display: inline-grid;
    padding-left: 25%;
    width: 100%
}

.cancer-detection-prediction {
    text-align: center;
    padding-top: 0.5%;
    width: 50%;
}

.cancer-detection-total-predictions {
    /* text-align: left;
    width: 20%;
    padding-left: 17%;
    padding-top: 1.5% */
}

.cancer-detection-loading {
    text-align: center;
    padding-top: 0.5%;
    width: 50%;
    animation: blinking 1s infinite
}

/* https://html-online.com/articles/blinking-text-css-animation/ */
@keyframes blinking { 
    0% {color: #000}
    49% {color: #000}
    50% {color: transparent}
    99% {color: transparent}
    0% {color: #000}
}

.fileContainer {
    box-shadow: 2px 2px 3px 5px rgba(0.05, 0.05, 0.05, 0.05);
    padding: 20px 20px
}